/*=============== GOOGLE FONTS ===============*/
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Volkhov:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap");
/*=============== VARIABLES CSS ===============*/
:root {
  --header-height: 3rem;
  /*========== Colors ==========*/
  --primary-color: #df6951;
  --white: #fff;
  --text-color: #181e4b;
  --text-color-1: #14183e;
  --text-color-2: #5e6282;
  --text-color-3: #212832;
  /*========== Font and typography ==========*/
  --body-font: "Poppins", sans-serif;
  --biggest-font-size: 7.5rem;
  --bigger-font-size: 4.2rem;
  --big-font-size: 2rem;
  --normal-font-size: 1.6rem;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-light: hsl(230, 22%, 74%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle-light: hsl(230, 19%, 60%);
  --toggle-button-light: hsl(228, 46%, 96%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-card-hover: hsl(228, 33%, 91%);
  --light-text1: hsl(230, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
  --background: var(--light-bg);
  --text-color: var(--light-text2);
  --text-color2: var(--light-text1);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--toggle-button-light);
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: var(--dark-bg);
    --text-color: var(--dark-text2);
    --text-color2: var(--dark-text1);
    --card-bg: var(--dark-card);
    --card-hover: var(--dark-card-hover);
    --toggle: var(--light-bg);
    --toggle-bg: linear-gradient(
      225deg,
      var(--toggle-bg-end) 0%,
      var(--toggle-bg-start) 98.02%
    );
    --toggle-button: var(--dark-bg);
  }
}
body.light {
  --background: var(--light-bg);
  --text-color: var(--light-text2);
  --text-color2: var(--light-text1);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--toggle-button-light);
}

body.dark {
  --background: var(--dark-bg);
  --text-color: var(--dark-text2);
  --text-color2: var(--dark-text1);
  --card-bg: var(--dark-card);
  --card-hover: var(--dark-card-hover);
  --toggle: var(--light-bg);
  --toggle-bg: linear-gradient(
    225deg,
    var(--toggle-bg-end) 0%,
    var(--toggle-bg-start) 98.02%
  );
  --toggle-button: var(--dark-bg);
}

/*=============== BASE ===============*/
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth;
  font-size: 62.5%;
  overflow-x: hidden;
}

body {
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  background-color: var(--bg-light-1);
  color: var(--text-color-1);
  overflow-x: hidden;
}

h1,
h2,
h3 {
  color: var(--text-color);
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

button,
input {
  border: none;
  outline: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
}

.heading-primary {
  font-family: Volkhov, serif;
  font-size: var(--biggest-font-size);
  color: #181e4b;
  font-weight: 700;
  line-height: 8rem;
  margin-bottom: 3rem;
}

.section-title {
  font-family: Volkhov, serif;
  font-size: var(--bigger-font-size);
  color: var(--text-color-1);
  font-weight: 700;
  margin-bottom: 6.5rem;
}

.section-title--center {
  text-align: center;
  margin-bottom: 6rem;
}

.section-subheading {
  font-size: 1.8rem;
  font-weight: 600;
  color: var(--text-color-2);
  text-transform: uppercase;
  margin-bottom: 1rem;
}

.section-subheading--center {
  text-align: center;
}

.heading-tertiary {
  font-size: var(--big-font-size);
  color: var(--text-color);
  font-weight: 700;
  margin-bottom: 3rem;
}

.btn {
  padding: 1.9rem 2.5rem;
  border-radius: 1rem;
  color: #fff;
  font-size: var(--normal-font-size);
  font-weight: 500;
  line-height: 1.4rem;
  background-color: #f1a501;
  transition: all 0.2s;
}
.btn:hover {
  filter: brightness(95%);
}

.btn--rounded {
  border-radius: 100rem;
}

.btn--line {
  box-shadow: inset 0 0 0 1px #212832;
}

.white-color {
  color: #fff;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (max-width: 87.4375em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
  color: var(--text-color2);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  max-width: 120rem;
  padding: 0 1.5rem;
  margin: 0 auto;
}
@media (max-width: 87.4375em) {
  .container {
    padding: 0;
  }
}

/*=============== LAYOUT ===============*/
.main {
  overflow: hidden;
}

.grid {
  display: grid;
}
.grid:not(:last-child) {
  margin-bottom: 9.6rem;
}

.grid--2-cols {
  grid-template-columns: repeat(2, 1fr);
}

.grid--3-cols {
  grid-template-columns: repeat(3, 1fr);
}

.grid--4-cols {
  grid-template-columns: repeat(4, 1fr);
}

.grid--5-cols {
  grid-template-columns: repeat(5, 1fr);
}

.grid--center-v {
  align-items: center;
}

.header {
  width: 100%;
  padding: 2.5rem 0;
  z-index: 10000;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  position: absolute;
  top: 0;
}
.header .container {
  flex: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__nav-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 5rem;
}
.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 5rem;
}
.header__nav-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 5rem;
}
.header__nav-link {
  font-size: 1.7rem;
  color: #212832;
  transition: all 0.2s;
}
.header__nav-link:hover {
  color: #4d535b;
}
.header__cta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 5rem;
}
.header__cta-login {
  color: #212832;
  transition: all 0.2s;
}
.header__cta-login:hover {
  color: #4d535b;
}
.header__cta-signup {
  padding: 0.7rem 2.1rem;
  border-radius: 0.5rem;
  color: #212832;
  box-shadow: inset 0 0 0 1px #212832;
  transition: all 0.3s;
}
.header__cta-signup:hover {
  background-color: var(--primary-color);
  color: #fff;
  box-shadow: none;
}
.header__lang {
  text-transform: uppercase;
  font-size: 1.7rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0.65rem;
  cursor: pointer;
  transition: all 0.2s;
  position: relative;
}
.header__lang:hover {
  color: #4d535b;
}
.header__lang:hover .header__lang-list {
  display: block;
}
.header__lang-list {
  display: none;
  position: absolute;
  top: 30px;
  right: 0;
  background-color: #fff;
  padding: 1.5rem 1rem;
  border-radius: 0.5rem;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

.btn-mobile-nav {
  border: none;
  background: none;
  cursor: pointer;
  display: none;
  z-index: 20000;
}

.mobile-nav-icon {
  font-size: 2.8rem;
}

.mobile-nav-icon[name=close-outline] {
  display: none;
}

.hero {
  background: url("/images/hero-bg.svg");
  background-repeat: no-repeat;
  background-position: right -300px top -730px;
  background-size: cover;
  padding: 10.5rem 0 5rem 0;
  overflow: hidden;
  position: relative;
  margin-bottom: 5rem;
}
.hero__bg-svg {
  display: none;
  position: absolute;
  right: 0;
  top: -10%;
}
.hero .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.hero .heading-primary {
  position: relative;
}
.hero .heading-primary::before {
  content: url(/images/brush.svg);
  width: 70%;
  position: absolute;
  top: 10px;
  right: -70px;
  z-index: -1;
}
.hero__left {
  max-width: 54.5rem;
}
.hero__right {
  flex: 1;
}
.hero__name {
  font-size: 1.7rem;
  color: var(--primary-color);
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 1.5rem;
}
.hero__text {
  color: var(--text-color-2);
  font-weight: 500;
  line-height: 3rem;
  margin-bottom: 3rem;
}
.hero__cta {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 4rem;
}
.hero__demo {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  cursor: pointer;
  transition: all 0.3s;
}
.hero__demo:hover ion-icon {
  transform: scale(1.1);
}
.hero__demo ion-icon {
  background-color: var(--primary-color);
  padding: 1.5rem;
  font-size: 2rem;
  color: var(--white);
  border-radius: 50%;
  transition: all 0.3s;
}

.service {
  margin-bottom: 15rem;
  position: relative;
  text-align: center;
}
.service .grid {
  column-gap: 2rem;
}
.service .grid::after {
  content: url(/images/service-dots.svg);
  position: absolute;
  top: -2rem;
  right: 0rem;
}
.service__cards {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 2rem;
}
.service__box {
  background-color: #fff;
  position: relative;
  transition: all 0.2s;
  padding: 3rem;
  z-index: 10;
  flex-basis: 26.7rem;
  min-width: 23rem;
}
.service__box:hover {
  box-shadow: 0px 100px 80px rgba(0, 0, 0, 0.02), 0px 64.8148px 46.8519px rgba(0, 0, 0, 0.0151852), 0px 38.5185px 25.4815px rgba(0, 0, 0, 0.0121481), 0px 20px 13px rgba(0, 0, 0, 0.01), 0px 8.14815px 6.51852px rgba(0, 0, 0, 0.00785185), 0px 1.85185px 3.14815px rgba(0, 0, 0, 0.00481481);
  border-radius: 36px;
  z-index: 100;
}
.service__icon {
  margin-bottom: 2.5rem;
}
.service__title {
  font-family: "Open Sans", sans-serif;
  font-size: 2rem;
  font-weight: 600;
  color: #1e1d4c;
  margin-bottom: 1.5rem;
}
.service__text {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  line-height: 2.6rem;
  font-weight: 500;
}

.destination {
  padding: 0 0 15rem 0;
  position: relative;
}
.destination__list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 2rem;
  position: relative;
}
.destination__list::after {
  content: url(/images/spiral.svg);
  position: absolute;
  right: 50px;
  bottom: 100px;
  z-index: -100;
}
.destination__card {
  display: grid;
  grid-template-columns: 31.4rem;
  grid-template-rows: 32.7rem 13rem;
  grid-template-areas: "img" "text";
  z-index: 100;
  transition: all 0.3s;
  cursor: pointer;
  border-radius: 2.4rem;
  overflow: hidden;
  box-shadow: rgba(17, 17, 26, 0.1) 0px 0px 16px;
}
.destination__card:hover {
  transform: translateY(-5px);
}
.destination__card:nth-child(1) {
  justify-self: end;
}
.destination__card:nth-child(3) {
  justify-self: start;
}
.destination__img-box {
  grid-area: img;
  overflow: hidden;
}
.destination__text-box {
  grid-area: text;
  padding: 2.8rem 2.2rem;
  z-index: 100;
  background-color: #fff;
}
.destination__text-box p {
  color: var(--text-color-2);
}
.destination__text-box div {
  display: flex;
}
.destination__text-box-1 {
  justify-content: space-between;
  margin-bottom: 2rem;
}
.destination__text-box-1 p {
  font-size: 1.8rem;
}
.destination__text-box-2 {
  gap: 1rem;
}

.book {
  margin-bottom: 15rem;
}
.book .grid {
  align-items: center;
}
.book__left {
  justify-self: center;
}
.book .section-title {
  margin-bottom: 3rem;
}
.book__content {
  display: flex;
  flex-direction: column;
  gap: 4rem;
}
.book__box {
  display: flex;
  gap: 2rem;
}
.book__text-box {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.book__title {
  font-weight: 700;
  color: var(--text-color-2);
}
.book__text {
  color: var(--text-color-2);
}

.brand {
  padding: 0 0 15rem 0;
}
.brand .grid {
  align-items: center;
  justify-items: center;
  column-gap: 0.5rem;
}
.brand__img {
  justify-self: center;
  align-items: center;
  filter: grayscale(100%);
  transition: all 0.3s;
  cursor: pointer;
  background: #ffffff;
}
.brand__img:hover {
  filter: grayscale(0%);
}

.subscribe {
  padding: 0 0 15rem 0;
  text-align: center;
}
.subscribe__box {
  padding: 7.9rem 0;
  background: #f4f1fd;
  border-radius: 129px 20px 20px 20px;
  position: relative;
}
.subscribe__box::before {
  content: url(/images/subs-send.svg);
  position: absolute;
  right: -25px;
  top: -20px;
}
.subscribe__box::after {
  content: url(/images/subs-dots.svg);
  position: absolute;
  right: -100px;
  bottom: -75px;
}
.subscribe__text {
  font-size: 3.3rem;
  font-weight: 600;
  color: var(--text-color-2);
  line-height: 5.4rem;
  margin-bottom: 7rem;
}
.subscribe .form {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2.4rem;
  z-index: 1000;
}
.subscribe .form .input {
  display: flex;
  align-items: center;
  gap: 1.4rem;
  padding: 1.8rem 3rem;
  background-color: #fff;
  border-radius: 1rem;
  width: 42.1rem;
}
.subscribe input {
  background-color: transparent;
  width: 100%;
}
.subscribe input::placeholder {
  font-size: 1.4rem;
  color: #39425d;
}
.subscribe .btn {
  cursor: pointer;
  padding: 2rem 5rem;
  background: linear-gradient(180deg, #ff946d 0%, #ff7d68 100%);
}

.footer {
  padding: 0 0 8.5rem 0;
}
.footer .grid {
  grid-template-columns: 2fr repeat(3, 1fr) 2fr;
  justify-items: center;
}
.footer__logo {
  font-size: 4.4rem;
  font-weight: 500;
  line-height: 4.4rem;
  margin-bottom: 1.8rem;
}
.footer__text {
  font-size: 1.3rem;
  color: var(--text-color-2);
  line-height: 1.5rem;
}
.footer__title {
  font-size: 2.1rem;
  font-weight: 700;
  margin-bottom: 3.4rem;
}
.footer__list {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}
.footer__link {
  font-size: 1.8rem;
  color: var(--text-color-2);
}
.footer__social-link {
  display: flex;
  align-items: center;
  gap: 2.5rem;
  margin-bottom: 2.5rem;
}
.footer__social-link a {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 4rem;
  width: 4rem;
  border-radius: 50%;
  padding: 1.6rem;
  background: #ffffff;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
  transition: all 1s;
}
.footer__social-link a:hover {
  background: conic-gradient(from 180deg at 50% 50%, #b8d2f1 0deg, #f289aa 60deg, #c68bf0 106.09deg, #d164da 153.75deg, #c963e8 221.25deg, #bfc2e8 258.75deg, #ffc999 288.75deg, #d0d8c9 315deg, #bad0f1 334.13deg, #ced8cb 358.97deg, rgba(255, 255, 255, 0) 360deg);
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
}
.footer__social-link a:hover .footer__social-icon {
  color: #fff;
}
.footer__social-icon {
  font-size: 5rem;
  color: #000;
}
.footer__social-text {
  font-size: 2rem;
  font-weight: 500;
  color: var(--text-color-2);
  margin-bottom: 1.5rem;
}
.footer__app {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.copyright {
  text-align: center;
  font-size: 1.4rem;
}

@media (max-width: 80em) {
  .hero {
    background-position: right -256px top -716px;
  }

  .hero .heading-primary {
    font-size: 6.5rem;
  }
  .hero .heading-primary::before {
    width: 65%;
    right: 0;
  }

  .subscribe__box::before {
    display: none;
  }
  .subscribe__box::after {
    display: none;
  }

  .destination::after {
    display: none;
  }

  .service::after {
    display: none;
  }
}
@media (max-width: 69em) {
  html {
    font-size: 56.25%;
  }

  .hero__name {
    font-size: 1.5rem;
  }

  .header {
    padding: 3rem 0;
  }
}
@media (max-width: 57em) {
  html {
    font-size: 50%;
  }

  .container {
    padding: 0 2.5rem;
  }

  .hero {
    background-image: none;
  }

  .header__nav {
    background-color: #fff;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    transform: translateX(-100%);
    z-index: 1000;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 3rem;
    transition: all 0.5s ease-in;
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
  }

  .btn-mobile-nav {
    display: block;
    z-index: 10000;
  }

  .nav-open .header__nav {
    opacity: 1;
    pointer-events: auto;
    visibility: visible;
    transform: translateX(0);
  }

  .header__nav-list {
    display: flex;
    flex-direction: column;
  }

  .header__nav-link {
    font-size: 1.8rem;
  }

  .header__cta {
    display: flex;
    flex-direction: column;
    font-size: 1.8rem;
    gap: 3rem;
  }

  .nav-open .mobile-nav-icon[name=close-outline] {
    display: block;
  }

  .nav-open .mobile-nav-icon[name=grid-outline] {
    display: none;
  }

  .destination__list::after {
    display: none;
  }

  .footer .grid {
    grid-template-columns: repeat(3, 1fr) 2fr;
    grid-template-rows: min-content min-content;
    justify-items: start;
    row-gap: 3rem;
  }
  .footer__brand {
    grid-row: 1;
    grid-column: 1/-1;
    justify-self: start;
  }

  .subscribe__text {
    font-size: 2.5rem;
    line-height: 4.4rem;
    margin-bottom: 4rem;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }
}
@media (max-width: 46.875em) {
  .hero .container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 4rem;
  }
  .hero__right {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .hero__right img {
    width: 80%;
  }
  .hero .heading-primary::before {
    display: none;
  }
  .hero__left {
    max-width: 80%;
  }
}
@media (max-width: 37.5em) {
  .hero__left {
    max-width: 100%;
  }
  .hero .heading-primary {
    font-size: 5rem;
    line-height: 6rem;
  }
  .hero .heading-primary::before {
    display: none;
  }

  .book .grid {
    display: flex;
    flex-direction: column;
    gap: 3rem;
    align-items: flex-start;
  }

  .subscribe__box {
    border-radius: 20px 20px 20px 20px;
    padding: 5rem 2rem;
  }
  .subscribe .form {
    gap: 1.5rem;
  }
  .subscribe .form .input {
    padding: 1.5rem 2rem;
  }
  .subscribe .btn {
    padding: 2rem 2rem;
  }

  .footer .grid {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: min-content min-content min-content;
    justify-items: start;
    row-gap: 4rem;
  }
  .footer__brand {
    height: min-content;
    grid-row: 1;
    grid-column: 1/-1;
  }
  .footer__social {
    grid-column: 1/-1;
  }
}
@media (max-width: 25em) {
  .hero__right img {
    max-width: 100%;
  }

  .subscribe__box {
    padding: 4rem 2rem;
  }

  .footer .grid {
    display: flex;
    flex-direction: column;
  }
  .footer__title {
    margin-bottom: 2rem;
  }
}
.toggle {
  display: grid;
  grid-template-columns: 1fr 3rem;
  border: none;
  margin: 0;
}
.toggle label {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--toggle);
}
.toggle label[for=dark] {
  line-height: 1.5rem;
  margin-right: 0.8125rem;
}
.toggle__wrapper {
  position: relative;
  height: 1.5rem;
}
.toggle input[type=radio] {
  margin: 0 -0.125rem 0 -0.125rem;
  opacity: 0;
  width: 1.5rem;
  height: 1.5rem;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 2px solid white;
}
.toggle__background {
  display: block;
  height: 100%;
  position: absolute;
  width: 100%;
  top: 0;
  border-radius: 0.75rem;
  background: var(--toggle-bg);
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--toggle-button);
  transition: all 150ms ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 21px);
  right: 3px;
}
.toggle #system:checked ~ .toggle__button {
  left: 50%;
  right: auto;
  transform: translate(-50%);
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (max-width: 57em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 87.4375em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  position: relative;
  overflow: hidden;
  background: var(--card-bg);
  color: var(--dark-text1);
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  text-align: center;
  transition: background 150ms ease-in-out;
  cursor: pointer;
}
.card:hover {
  background: var(--card-hover);
}
.card--facebook {
  border-top: 0.3125rem solid var(--facebook);
}
.card--twitter {
  border-top: 0.3125rem solid var(--twitter);
}
.card--instagram {
  padding-top: 1.875rem;
}
.card--instagram::before {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  top: 0;
  width: 100%;
  height: 0.3125rem;
  background: linear-gradient(225deg, var(--instagram-end), var(--instagram-middle) 50.91%, var(--instagram-start) 100%);
}
.card--youtube {
  border-top: 0.3125rem solid var(--youtube);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 1.25rem;
  margin-top: 0.3125rem;
  margin-bottom: 1.75rem;
}
.card__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text-color2);
}
.card__icon {
  margin-right: 0.5rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: 700;
  color: var(--text-color2);
}
.card__followers {
  margin-bottom: 1.5625rem;
}
.card__count {
  color: var(--text-color);
  font-weight: 700;
  letter-spacing: -0.125rem;
  line-height: 1;
  margin-bottom: 0.25rem;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__count--small {
  font-size: 2rem;
}
.card__label {
  font-size: 0.75rem;
  letter-spacing: 0.3125rem;
  font-weight: 400;
  color: var(--text-color2);
  text-transform: uppercase;
}
.card__change {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75rem;
  font-weight: 700;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}
.card__change img {
  margin-right: 0.25rem;
}
.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  justify-items: start;
  gap: 1.4375rem;
}
.card-grid .card__count {
  margin-bottom: 0;
}
.card-grid .card__count,
.card-grid .card__change {
  align-self: end;
}
.card-grid img,
.card-grid .card__change {
  justify-self: end;
}